import React, {Component} from "react";
import './styles.css';
import image from './img/owl.jpg';

export default class Main extends Component {
    

    render() {
        return (
            <div className="main-wrapper">
                <h1 className="cool-text">Неймовірно крутий текст</h1>
                <div className="picture-wrapper">
                    <img 
                        className="picture"
                        src={image}
                        alt="owl"/>
                </div> 
            </div>
        ) 
    }
}; 